












































































































































































































import Vue from 'vue';
import { Prop } from 'vue/types/options';
import {
  Form,
  FormItem,
  Input,
  InputNumber,
  Menu,
  MenuItem,
  Option,
  Select,
} from 'element-ui';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import {
  faInfoCircle,
  faBookOpen,
  faPencilAlt,
} from '@fortawesome/free-solid-svg-icons';
import { ITableProperties, TablePropertyKeys } from '@cassandratypes/cassandra';
import HelpfulFormItem from '@/components/common/HelpfulFormItem.vue';
import startCase from 'lodash.startcase';

export default Vue.extend({
  name: 'CassTablePropertiesEditor',
  components: {
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Input.name]: Input,
    [InputNumber.name]: InputNumber,
    [Menu.name]: Menu,
    [MenuItem.name]: MenuItem,
    [Option.name]: Option,
    [Select.name]: Select,
    FontAwesomeIcon,
    HelpfulFormItem,
  },
  props: {
    value: {
      type: Object as Prop<ITableProperties>,
    },
    disabled: {
      type: Boolean,
    },
    suppressProperties: {
      type: Array as Prop<TablePropertyKeys[]>,
      default: () => [],
    },
  },
  data() {
    return {
      faBookOpen,
      faInfoCircle,
      faPencilAlt,
      validationRules: {},
      selectedCategory: 'general',
      rowCaching: undefined as string | undefined,
      retryOptions: [
        {
          label: 'Always',
          value: 'ALWAYS',
        },
        {
          label: '95th Percentile',
          value: '95percentile',
        },
        {
          label: '99th Percentile',
          value: '99percentile',
        },
        {
          label: 'None',
          value: 'NONE',
        },
      ],
      cachingOptions: [
        {
          label: 'All',
          value: 'ALL',
        },
        {
          label: 'None',
          value: 'NONE',
        },
      ],
      rulesWriteForm: {
        'compression.chunk_length_kb': [
          {
            required: true,
            message: 'Chunk length is required',
          },
          {
            validator: (_rule, value: string, cb) => {
              const num = Number.parseInt(value);
              if ((num & (num - 1)) !== 0) {
                return cb(
                  new Error(
                    'Compression chunk length must be a valid power of 2',
                  ),
                );
              }
              cb();
            },
          },
        ],
      },
    };
  },
  methods: {
    getDisplayName(str: string) {
      const nameMapping = {
        sstable_size_in_mb: 'SS Table Size (MB)',
      };
      return nameMapping[str] || startCase(str);
    },
    onSelectCategory(category) {
      this.selectedCategory = category;
    },
    onValueChanged() {
      this.$emit('input', this.value);
      this.$emit('change', this.value);
    },
  },
});
