




































import Vue from 'vue';
import { Form, FormItem, Input, Table, TableColumn } from 'element-ui';
import { Routes } from '@/router/routes';
import store from '@/store';
import { IUserDefinedType } from '@cassandratypes/cassandra';
import { ClientTable } from 'vue-tables-2';

Vue.use(ClientTable);

export default Vue.extend({
  name: 'CassKeyspaceUdtsView',
  components: {
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Input.name]: Input,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  props: {
    clusterName: {
      type: String,
      required: true,
    },
    keyspaceName: {
      type: String,
      required: true,
    },
    udtName: {
      type: String,
    },
  },
  data() {
    return {
      Routes,
      tableOptions: {
        filterByColumn: true,
        perPage: 100,
        headings: {
          name: 'UDT Name',
        },
      },
      tableColumns: ['name'],
      tablesLoading: false,
    };
  },
  computed: {
    udts(): IUserDefinedType[] {
      return store.state.cassandra.explore.keyspaceUDTs;
    },
    selectedUdt(): IUserDefinedType | undefined {
      if (!this.udtName || !this.udts) {
        return undefined;
      }
      return this.udts.find((udt) => udt.name === this.udtName);
    },
  },
});
